<!--suppress JSUnresolvedReference -->
<script>
export default {
  name: "ProductHover",
  data() {
    return {
      hover: false,
      productList: [
        {
          img: require("@/assets/images/餐食配图/贝果系列/1.jpeg"),
          title: "LIGHT MEAL",
          target: 'blank',
          href: '/coffee/gourmet'
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/咖啡商品/7.jpg"),
          title: "COFFEE",
          target: 'scroll',
          href: 'secondPage'
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/7.jpg"),
          title: "FRESH COFFEE",
          target: 'scroll',
          href: 'threePage'
        },
      ]
    }
  },
  mounted() {
    this.$bus.$on("productHover", (status) => {
      this.hover = status
    })
  },
  methods: {
    moveTp({target, href}) {
      if (target === 'blank') {
        this.$router.push(href)
      } else {
        console.log(this.$route.path)
        if (this.$route.path === '/coffee/home') {
          document.getElementById(href).scrollIntoView({
            behavior: "smooth",// 过渡动画效果 详细看文档
            block: "start" // 滚动到的位置 可选start center end
          });
        } else {
          this.$router.push({path: '/coffee/home', query: {scroll: href}})
        }
      }
    }
  }
}
</script>

<template>
  <div
      class="absolute top-20 left-0 shadow-2xl bg-white h-52 w-full z-60" @mousemove="hover = true"
      @mouseleave="hover = false"
      v-show="hover">
    <div class="bg-grey w-full h-full flex items-center justify-center">
      <div v-for="(item,index) in productList" class="w-48 h-32 text-center px-5 cursor-pointer" :key="index"
           @click="moveTp(item)">
        <img class="w-full h-full mb-2 transition hover:scale-110 transform" :src="item.img" alt="">
        <span class="font-bold text-coffee">{{ item.title }}</span>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
.bg-grey {
  background: url("@/assets/bg/texture-overlay--grey.png");
}
</style>