<!--suppress JSUnresolvedReference -->
<script>
import storage from "@/utils/storage";

export default {
  name: "CoffeeHeader",
  data() {
    return {
      lang: 'tc',
      langList: [
        {value: 'tc', name: '繁'},
        {value: 'sc', name: '简'},
        {value: 'en', name: 'ENG'}
      ]
    }
  },
  computed: {
    langName() {
      return this.langList.find(item => item.value === this.lang).name;
    }
  },
  mounted() {
    this.selectLangHandle(storage.getLanguage());
  },
  methods: {
    selectLangHandle(type) {
      this.lang = type;
      this.$i18n.locale = type;
      storage.setLanguage(type);
    },
    scrollTo(name) {
      if (this.$route.path === '/coffee/home') {
        document.getElementById(name).scrollIntoView({
          behavior: "smooth",// 过渡动画效果 详细看文档
          block: "start" // 滚动到的位置 可选start center end
        });
      } else {
        this.$router.push({path: '/coffee/home', query: {scroll: name}})
      }
    }
  }
}
</script>

<template>
  <div class="w-full  border-b border-gray-200 absolute top-0 left-0 z-50 text-coffee bg-gray-50 bg-opacity-5">
    <div class="container mx-auto h-20 flex items-center justify-between">
      <div @click="$router.push('/coffee')" class="cursor-pointer">
        <img src="@/assets/visit.png" alt="logo" class="h-16"/>
      </div>
      <ul class="flex h-full items-center nav">
        <li :class="{'text-coffeeLight':$route.path === '/coffee/home'}" @click="$router.push('/coffee')">Home</li>
        <li :class="{'text-coffeeLight':$route.path === '/coffee/gourmet'}"
            @mouseenter="$bus.$emit('productHover',true)"
            @mouseleave="$bus.$emit('productHover',false)">
          <span>products</span>
          <i class="el-icon-arrow-down ml-1"></i>
        </li>
        <li @click="scrollTo('fourPage')">Shop</li>
        <li :class="{'text-coffeeLight':$route.path === '/coffee/boutique'}" @click="$router.push('/coffee/boutique')">
          Boutique
        </li>
        <li :class="{'text-coffeeLight':$route.path === '/coffee/history'}" @click="$router.push('/coffee/history/')">
          History
        </li>
        <li @click="scrollTo('sixPage')">More</li>
      </ul>
      <div>
        <el-dropdown>
          <span class="flex items-center">
            <img src="@/assets/icon/language.png" alt="">
            <span class="px-1">{{ langName }}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="item in langList" :key="item.value"
                              @click="selectLangHandle(item.value)">
              <p :class="{'text-coffeeLight': item.value === lang}">{{ item.name }}</p>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
.language-selector {
  background-color: orange;
  color: black;
  border-radius: 5px;
}

.nav {
  li {
    padding: 0 1.5rem;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    transition: all .3s;

    &:hover {
      background-color: rgba(188, 155, 93, 0.5);
    }
  }
}
</style>